import { GLShaderCommon } from '../Utility/GLShaderCommon';
import { GeometryBase } from './Base/GeometryBase';
import { Scene } from '../Scene';
export class Tri extends GeometryBase {
    v = `#version 300 es
        in vec3 a_position;

        uniform float uPointSize;

        void main(void) {
            gl_PointSize = uPointSize;
            gl_Position = vec4(a_position, 1.0);
        }
    `;
    f = `#version 300 es
        precision mediump float;

        out vec4 finalColor;

        void main(void) {
            finalColor = vec4(0.0, 0.0, 0.0, 1.0);
        }
    `;

    gl: WebGL2RenderingContext;

    vShader: WebGLShader;
    fShader: WebGLShader;

    program: WebGLProgram;

    aPositionLoc: number;
    uPointSizeLoc: WebGLUniformLocation | null;

    size: number = 100;
    time: number = 0.0;
    constructor(scene: Scene, gl: WebGL2RenderingContext) {
        super(scene);
        this.gl = gl;
        this.initShader(gl);
    }
    initShader(gl: WebGL2RenderingContext) {
        let vShader = GLShaderCommon.createShader(gl, gl.VERTEX_SHADER as unknown as WebGLShader, this.v);
        let fShader = GLShaderCommon.createShader(gl, gl.FRAGMENT_SHADER as unknown as WebGLShader, this.f);
        if (vShader && fShader) {
            this.vShader = vShader;
            this.fShader = fShader;
            let program = GLShaderCommon.createProgram(gl, this.vShader, this.fShader, true);

            if (program) {
                this.program = program;
                gl.useProgram(this.program);
                this.aPositionLoc = gl.getAttribLocation(this.program, 'a_position');
                this.uPointSizeLoc = gl.getUniformLocation(this.program, 'uPointSize');
                gl.useProgram(null);

                this.initGeo(gl);

            }
        } else {
            console.error('vertex fragment shader error');
        }
    }
    initGeo(gl: WebGL2RenderingContext) {
        let aryVerts = new Float32Array([
            0, 0.5, 0,
            0.5, -0.5, 0,
            -0.5, -0.5, 0
        ]);
        let bufVerts = gl.createBuffer();

        gl.bindBuffer(gl.ARRAY_BUFFER, bufVerts);
        gl.bufferData(gl.ARRAY_BUFFER, aryVerts, gl.STATIC_DRAW);
        gl.bindBuffer(gl.ARRAY_BUFFER, null);

        gl.useProgram(this.program);
        gl.uniform1f(this.uPointSizeLoc, 50.0);

        // how its down without VAOs
        gl.bindBuffer(gl.ARRAY_BUFFER, bufVerts);
        gl.enableVertexAttribArray(this.aPositionLoc);
        gl.vertexAttribPointer(this.aPositionLoc, 3, gl.FLOAT, false, 0, 0);
        gl.bindBuffer(gl.ARRAY_BUFFER, null);

    }
    Render(gl: WebGL2RenderingContext) {
        gl.uniform1f(this.uPointSizeLoc, 15 + (this.size * (Math.cos(this.time) + 1)));
        gl.drawArrays(gl.POINTS, 0, 3);
        this.time += 0.1;
    }
    Dummy() {
        //
    }
}