import { WebGL2 } from '..';

export class CoreWebg2 {
    app: WebGL2;

    gl: WebGL2RenderingContext;
    canvas: HTMLCanvasElement;

    constructor(app: WebGL2) {
        this.app = app;

        this.canvas = document.createElement('canvas');
        if (this.app.divHost) {
            this.app.divHost.appendChild(this.canvas);
            this.canvas.width = this.app.divHost.clientWidth;
            this.canvas.height = this.app.divHost.clientHeight;
        }
        const gl = this.canvas.getContext('webgl2');
        if (gl) { this.gl = gl; }

        this.loop(this.gl);
    }
    loop(gl: WebGL2RenderingContext) {
        requestAnimationFrame(() => { this.loop(gl); });
        this.clear(gl);
        this.Render(gl);
    }
    Render(gl: WebGL2RenderingContext) {
        //
    }
    clear(gl: WebGL2RenderingContext) {
        gl.clearColor(1.0, 1.0, 1.0, 1.0);
        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
    }
}
