import { Tri } from '../Geometry/Tri';

import { GeometryBase } from '../Geometry/Base/GeometryBase';
import { WebGL2 } from '..';
export class Scene {
    app: WebGL2;
    list: GeometryBase[] = [];
    constructor(app: WebGL2) {
        this.app = app;
        this.app.coreWebg2.Render = this.Render.bind(this);
        this.Init();
    }
    Init() {
        const d = new Tri(this, this.app.coreWebg2.gl);
        d.Dummy();
    }
    Render(gl: WebGL2RenderingContext) {
        for (let i = 0; i < this.list.length; ++i) {
            this.list[i].Render(gl);
        }
    }
}